.div {
  form {
    background-color: rgb(16, 219, 219);
    border-radius: 0.4rem;
    width: 50%;
    margin: 1rem auto;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    text-align: center;
    input,
    button {
      width: 80%;
      margin: auto;
      border-radius: 0.3rem;
      border: none;
      margin-top: 0.5rem;
      padding: 0.4rem;
    }
    button {
      cursor: pointer;
    }
    @media screen and(max-width:1000px) {
      width: 80%;
    }
  }
}
