.navitems {
  ul {
    display: flex;
    li {
      padding-right: 1rem;
    }
  }
  @media screen and (max-width: 1000px) {
    border: 1px solid black;
    width: 100%;
    position: absolute;
    height: 92vh;
    top: 8vh;
    left: 0;
    right: 0;

    background: #000;
    ul {
      flex-direction: column;
      width: 100%;
      margin: 40% auto;

      li {
        display: block;
        margin: 1rem auto;
        font-size: 2rem;
        a {
          color: rgba(255, 217, 0, 0.558);
          &:hover {
            color: rgb(255, 217, 0);
          }
        }
      }
    }
  }
}
