.navbar {
  background: rgba(47, 255, 0, 0.236);
  position: fixed;
  width: 100%;
  height: 10vh;
  color: rgb(0, 49, 108);
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 1rem;
  position: relative;
  .navbar__logo {
    h1 {
      font-size: 1.3rem;
    }
  }
  @media screen and (max-width: 1000px) {
    .navbar__logo {
      font-size: 1.3rem;
    }
    .bars {
      display: block;
      font-size: 3rem;
      cursor: pointer;
    }
    .desktop {
      display: none;
    }
  }
}
