.apodMap {
  border: 1px solid #eee;
  box-shadow: 6px 6px 6px #eee;
  margin: 0 auto 0 auto;
  display: flex;
  flex-direction: column;
  border-radius: 0.4rem;
  width: 80%;
  align-items: center;
  .image-block {
    width: min($numbers: 70%);

    img {
      border-radius: 0.4rem;
      width: 80%;
    }
  }
  h1 {
    color: rgba(0, 0, 255, 0.562);
    font-size: 1.5rem;
    padding: 1rem;
  }
  p {
    padding: 0.5rem;
  }
  h2 {
    color: rgba(0, 0, 255, 0.562);
  }
  @media screen and (max-width: 2000px) {
    p {
      margin: 1rem;
      letter-spacing: 1px;
      font-size: 1.1rem;
    }
  }
}
